<template>
	<div>
		<Loader v-if="isLoading" />
		<v-data-table
			v-else
			class="mt-6"
			:headers="header"
			:items="dataJenjang"
			:items-per-page="10">
			<template v-slot:no-data>
	      <v-alert type="info" class="mt-4">
	        <div>Data tidak ditemukan</div>
	      </v-alert>
	    </template>
	    <template v-slot:item.no="{item}">
	    	{{dataJenjang.map(x => x.id).indexOf(item.id)+1}}    	
	    </template>
			<template v-slot:item.action="{item}">
				<v-btn icon color="success">
					<v-icon small>mdi-eye</v-icon>
				</v-btn>
				<EditJenjang :data="item" />
				<HapusJenjang :id="item.id" />
			</template>		
		</v-data-table>
	</div>
</template>

<script>
	import { mapState } from 'vuex'
	import EditJenjang from '@/components/Jenjang/EditJenjang'
	import HapusJenjang from '@/components/Jenjang/HapusJenjang'
	import Loader from '@/components/Loader'

	export default{
		data: () => ({
			header: [
				{ text: 'No', value: 'no', sortable: false },
				{ text: 'Nama ', value: 'nama', sortable: false },
				{ text: 'Action', value: 'action', align: 'center', sortable: false }
			],

			data: [
				{
					nama: 'SMA',
					id: 1
				}
			],
		}),

		components: {
			Loader,
			HapusJenjang,
			EditJenjang
		},

		created() {
			this.$store.dispatch('dataJenjang/getDataJenjang')
			this.$store.dispatch('dataJenjang/updateIsLoading', true)
		},

		computed: { ...mapState('dataJenjang', ['dataJenjang', 'isLoading']) }
	}
</script>